a {
	color: var(--yellow);
}

.center-navbar {
  text-align: center;
}
.center-navbar .navbar {
  display: inline-block;
}
.center-navbar .navbar .container {
  width: 100%;
}


.img-container {

}
.logo-image {
	height: 50px;
	width: 50px;
	background-color: white;
}

.hero {
	background-color: inherit;
}

.navbar-text {
	color: var(--yellow) !important;
	font-weight: 600;
	font-size: 1.5rem;
}

#first-pic {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	width: 100%;
	/*border: 3px solid black;*/
	flex-wrap: wrap;
}

.test { 
	padding: 50px;
	 }

.test p {
	font-weight: bold;
	font-size: 1rem;
}

@media (min-width: 576px) { 
}

@media (min-width: 768px) { 
}


@media (min-width: 992px) { 
	.yuujin-image {
		width: 100vw;
	}
}


@media (min-width: 1200px) { 
	.img-container {
		height: 800px;
		flex: 1 auto;
	}
	.yuujin-image {
		outline: 5px solid var(--yellow);
        transform: translateY(-50px);
		width: 100%;
	}
	.flex-row-lg {
		width: 100%;
		display: flex;
		flex-direction: row;
	}
	.test {
		padding: 0px 50px;
		display: flex;
		flex-direction: column;
		text-align: center;
		align-items: center;
		justify-content: center;
	}
}
